import Vue from 'vue'
import VueRouter from 'vue-router'

const Index=()=>import('../views/index/Index');
const Caffeine=()=>import('../views/caffeine/Caffeine')
const Footoes=()=>import('../views/footoes/Footoes')
const Clips=()=>import('../views/clips/Clips')


Vue.use(VueRouter);

const routes=[
  {
    path:'',
    component:Index
  },
  {
    path:'/index',
    component:Index
  },
  {
    path: '/caffeine',
    component:Caffeine
  },
  {
    path: '/footoes',
    component:Footoes
  },
  {
    path: '/clips',
    component:Clips
  }

];

// router对象
const router=new VueRouter({
  routes,
  mode:'history'
});


// 导航守卫，限制页面访问

export default router
